<template>
  <!--  -->
  <div class="text-center">
    
    <v-dialog
      v-model="dialog"
      width="510"
    >
    <!---->
      <template v-slot:activator="{ on, attrs }">
        
        <div
          v-bind="attrs"
          v-on="on"
          class="reset_pw_btn"
        >
           
        </div>
      </template>

      <!-- -->
      <v-card>

        <!-- -->
        <v-btn
            icon
            navy
            @click="dialog = false"
            class="rs-pw-close"
          >
            <v-icon small color="black" class="rs-pw-close-ic">mdi-close</v-icon>
        </v-btn>
        <!-- -->
        <v-card-title class="text-h5 rs-pw-cd-title">
           
        </v-card-title>

        
        <!-- -->
        <v-card-text class="rs-pw-cd-content">
          <!-- -->
          <div>
              <span class="rs-pw-input-title"></span>
              <input placeholder=" " class="cur_user_id" v-model="user.user_id" />
          </div>
          <!-- -->
          <div>
              <span class="rs-pw-input-title"> </span>
              <!-- <v-text-field label=" " type="password" :rules="new_pw1" v-model="user.password" class="reset_new_pw1"></v-text-field>
              <v-text-field label="  " type="password" :rules="new_pw2" class="reset_new_pw2"></v-text-field> -->
              <input placeholder=" " type="password" class="reset_new_pw1" v-model="user.user_pwd" />
              <input placeholder="  " type="password"  class="reset_new_pw2" v-model="user.confirm_user_pwd"/>
          </div>
        </v-card-text>
    
        <!-- -->
        <v-card-actions>
          
          <v-btn
            v-on:click="pwReset"
            text
            @click="dialog = false"
            class="rs-pw-cmplt"
          >
            
          </v-btn>
        </v-card-actions>
        
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  methods: {
    pwReset: function () {
      this.$http.post("/api/musers/pwReset", {
          user: this.user,
        })
        .then((res) => {
          if (res.data.success == true) {
            alert(res.data.message);
            this.$router.go();
          }
          if (res.data.success == false) {
            alert(res.data.message);
            this.$router.go();
            this.clearInput();
          }
        })
        .catch(function () {
          // alert("error");
        });
    },
    
  },
  data () {
        return {
          user: {
            user_id:"",
            user_pwd:"",
            confirm_user_pwd:""
          },
          // new_pw1: [
          //   v => this.state === 'ins' ? !!v || '  .' : true,
          // ],
          // new_pw2: [
          //   v => this.state === 'ins' ? !!v || '  .' : true,
          //   v => v === this.password || '  .'
          // ],
          dialog: false,
          notifications: false,
          sound: true,
          widgets: false,
        }
    },
}
</script>

<style>
  /*  */
  .reset_pw_btn{
    width: 100%;
    font-size: 16px !important;
    text-decoration: underline;
    line-height: 3.38;
    color: #493dcf !important;
  }
  /* */
  .rs-pw-cd-title{
    display: block !important;
    color: #313343 !important;
    font-weight: bold !important;
    text-align: center !important;
    padding-top:70px !important;
  }
  .rs-pw-cd-content{
    width:370px !important;
    margin:auto;
  }
  .rs-pw-input-title{
    width: 98px;
    height: 22px;
    font-size: 14px;
    text-align: left !important;
    color: #3b3b3b;
  }
  .rs-pw-cd-content div:first-child{
    margin-top:50px !important;
  }
  .rs-pw-cd-content div:nth-child(2){
    margin-top:50px !important;
  }
  .cur_user_id,
  .reset_new_pw1,
  .reset_new_pw2{
    display: block;
    width: 100%;
    font-size: 14px;
    height: 48px;
    margin-top:12px;
    padding: 9px 48px 9px 14px;
    border-radius: 6px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.16);
    background-color: #ffffff;
  }
  .cur_user_id:focus,
  .reset_new_pw1:focus,
  .reset_new_pw2:focus{
    outline: #493dcf;
    border: solid 1px #313343;

  }
  .rs-pw-cmplt{
    width: 190px;
    height: 50px;
    padding: 20px 100px !important;
    border-radius: 50px !important;
    margin: 50px auto 70px auto !important;
    border: solid 3px #313343;
    background-color: #313343;
    color : white !important;
    font-weight: bold;
  }
  .rs-pw-close{
    width: 25px !important;
    height: 25px !important;
    float: right;
    margin: 20px !important;
    background-color: rgba(17, 19, 67, 0.16) !important;
  }

</style>

