import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/components/user/Login'
import SignUp from '@/components/user/Signup'
import DashBoard from '@/components/DashBoard'
import MOAList from '@/components/moaList/MOAList'
import ListDetail from '@/components/moaList/ListDetail'
import DrawerNav from '@/components/DrawerNav'
import NotFound from '@/components/NotFound'
import SourceView from '@/components/moaList/SourceView'
import ContentDetail from '@/components/moaList/ContentDetail'


// import Upload from '@/components/moaList/Upload'
import Post from '@/components/moaList/Post'


// Vue VueRouter 
Vue.use(VueRouter);

//  route   
const routes = [
  {
    path:'/',
     redirect: '/login',
  },
  {
    path: '/',
    name: 'drawernav',
    component: DrawerNav,
    children:[
      {
        path:'login',
        component: Login,
        meta : {authRequired : false}
      },
      {
        path:'signup',
        component: SignUp,
        meta : {authRequired : false}
      },
      {
        path: 'dashboard',
        component: DashBoard,
        meta : {authRequired : true}
      },
      {
        path: 'moalist',
        component: MOAList,
        meta : {authRequired : true}
      },
      {
        path: 'addfile',
        component: MOAList,
        meta : {authRequired : true}
      },
    ]
  },
  {
    path: '/moalist/:id/',
    name: 'listdetail',
    component:ListDetail,
    children:[
      {
        path:'source',
        name: 'source',
        component: SourceView,
        props:true,
        meta : {authRequired : false}
      },
      {
        path:'content',
        name: 'content',
        component: ContentDetail,
        meta : {authRequired : false}
      },
    ],
    meta : {authRequired : true}
  },
  {
    path: '/post',
    name : 'posting',
    component : Post,
    meta : {authRequired : true}
  },
  {
        path: "/404",
        name: "notFound",
        component: NotFound
    },
  {
      path: '*',
      redirect: "/404"
  },
]

// VueRouter route  .
const router = new VueRouter({
  routes
})

// to :  URL   
// from :  url    
// next : to  URL     .
router.beforeEach((to, from, next) => {
  const loggedIn = JSON.parse(localStorage.getItem('token'))==null? false : true;
  console.log(to);
  console.log(from.path);
  console.log(next);
  console.log(loggedIn);


  //  
  if (to.matched.some(record => record.meta.authRequired)) { //    '/'  . 
      console.log('authRequired');
      if (loggedIn) {
        console.log('loggedin');
        next();
        return;
      }
      next('/');
  }
  else{
    console.log('NoauthRequired');
    if (loggedIn) {
        console.log('Butloggedin');
        // if(to.path == '/' || to.path == '/login' || to.path == '/signup'){
           next({path:'/moalist'});
           return;
        // }
      }
      next();
      
    }
  })
//  VueRouter .
export default router
